/**
 * 价格整数和小数部分切割
 * eg:参考 view/bizcomponent/goodslistnew/GoodsItemList.vue
 */
export const mycurrency = (value, mark) => {
    value = value || 0;
    var str = (value + '').split('.');
    var array = [];
    if (str.length === 1) {
        array[0] = value;
        array[1] = '00';
    } else {
        str.map((item, index) => {
            if (index === str.length - 1) {
                item = item.length < 2 ? item + '0' : item.substring(0, 2);
            }
            array[index] = item;
        });
    }
    return array[mark];
};

/**
 * 返回小数点2位
 */
export const mycurrency1 = (value) => {
    value = value || '0.00';
    return parseFloat(value.toString()).toFixed(2);
};

/**
 * 数量超过一万的时候就换单位
 */
export const myvolumes = value => {
    value = value || 0;
    if (value > 10000) {
        return parseInt(value / 10000) + '万';
    } else {
        return value;
    }
};

/**
 * 数量超过一亿的时候就换单位
 */
export const myvolumes100 = value => {
    value = value || 0;
    if (value >= 1000000) {
        return parseInt(value / 10000) + '万';
    } else {
        return value;
    }
};

/**
 * 电话号码13311112222改为133-1111-2222
 */
export const mytelnum = value => {
    if (!value) {
        return '';
    }
    if (value.length === 11) {
        return value.substr(0, 3) + '-' + value.substr(3, 4) + '-' + value.substr(7, 4);
    } else {
        return value;
    }
};
